@mixin make-position($prefix: '.') {
  @each $name,
    $value
      in (
        static: static,
        fixed: fixed,
        absolute: absolute,
        relative: relative,
        sticky: sticky
      )
  {
    #{$prefix + $name} {
      position: $value;
    }
  }
}

@include make-position();
@each $deivce in map-keys($devices) {
  @include media-device($deivce) {
    @include make-position('.' + selector-escape($deivce + ':'));
  }
}
