@mixin make-float($prefix: '.') {
  #{$prefix}float-right {
    float: right;
  }

  #{$prefix}float-left {
    float: left;
  }

  #{$prefix}float-none {
    float: none;
  }
}

@include make-float();
@each $deivce in map-keys($devices) {
  @include media-device($deivce) {
    @include make-float('.' + selector-escape($deivce + ':'));
  }
}
