/*!markdown

---
title: Box Sizing
---

Box Sizing  padding  border 


| Class       | Properties               |
| ----------- | ------------------------ |
| box-border  | box-sizing: border-box;  |
| box-content | box-sizing: content-box; |

## 

 box-border  128px 128px + (2 * 4px) + padding 2 * 16px 160px

```html
<div class="flex space-x-6">
  <div class="box-border h-32 w-32 p-4 border-4 border-blue-400 bg-blue-200 rounded-md">
    <div class="h-full w-full bg-red-400"></div>
  </div>

  <div class="box-content h-32 w-32 p-4 border-4 border-blue-400 bg-blue-200 rounded-md">
    <div class="h-full w-full bg-red-400"></div>
  </div>
</div>
```

## 

 [](../../../docs/style/responsive-design.md) [issue](https://github.com/baidu/amis/issues)

## 

[](../../../docs/style/state.md) [issue](https://github.com/baidu/amis/issues)

*/

@mixin make-box-sizing($prefix: '.') {
  #{$prefix}box-border {
    box-sizing: border-box;
  }

  #{$prefix}box-content {
    box-sizing: content-box;
  }
}

@include make-box-sizing();
