@mixin make-visibility($prefix: '.') {
  #{$prefix}visible {
    visibility: visible;
  }

  #{$prefix}invisible {
    visibility: hidden;
  }
}

@include make-visibility();
@each $deivce in map-keys($devices) {
  @include media-device($deivce) {
    @include make-visibility('.' + selector-escape($deivce + ':'));
  }
}
